import React, { useEffect, useState } from "react";
import "./TestDetails.scss";

import { Outlet, useParams } from "react-router-dom";
import { getTestDetails } from "../../Redux/TestDetailReducer/TestDetailSlice";
import { useDispatch, useSelector } from "react-redux";
import { NavLink, useNavigate } from "react-router-dom";

// icons
import { MdPriorityHigh } from "react-icons/md";
import { BsArrowLeftShort } from "react-icons/bs";
import { BiEdit } from "react-icons/bi";
import { CiUser } from "react-icons/ci";

// utils
import Button from "component/common/Button/Button";
import axiosWithAuth from "../../utils/axiosClienttok";
import { getActivityList } from "../../Redux/activityReducer/ActivityReducer";
import Dialog from "component/common/Dialog/Dialog";
import Page from "component/layout/Page/Page";
function TestDetails() {
  // states
  const [enableEdit, setEnableEdit] = useState(true);
  const [status, setStatus] = useState("");
  const [automationStatus, setAutomationStatus] = useState("");
  const [Priorities, setPriority] = useState("");
  const [reason, setReason] = useState("");
  const [reasonPop, SetReasonPop] = useState(false);

  const params = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const match = params.id;
  console.log(params);

  const testDetail = useSelector((state) => state.TestDetailReducer.testDetail);

  //  run on initial render and get the tesst detail  from redux
  useEffect(() => {
    dispatch(getTestDetails(match));
  }, []);

  async function updateTestInfo() {
    var statusP = testDetail?.status;
    var automationStatusP = testDetail?.automation_status;
    var priorityP = testDetail?.priority;
    var changed = [];

    if (status !== "") {
      statusP = status;
      changed.push("Status");
    }
    if (automationStatus !== "") {
      automationStatusP = automationStatus;
      changed.push("Automation Status");
    }
    if (Priorities !== "") {
      priorityP = Priorities;
      changed.push("Priority");
    }
    try {
      const response = await axiosWithAuth.patch(`/test-case/details/${match}/`, {
        status: statusP,
        automation_status: automationStatusP,
        priority: priorityP,
        history_change_reason: reason
      });
      console.log(changed);
      console.log(response);
    } catch (error) {
      console.log(error);
    } finally {
      dispatch(getTestDetails(match));
      dispatch(getActivityList(match));
      setEnableEdit(true);
      setAutomationStatus("");
      setPriority("");
      setStatus("");
      setReason("");
      SetReasonPop(false);
    }
  }
  const handleStatusChange = (e) => {
    setStatus(e.target.value);
  };

  const handleAutomationStatusChange = (e) => {
    setAutomationStatus(e.target.value);
  };

  const handlePriorityChange = (e) => {
    setPriority(e.target.value);
  };

  const formatClassName = (className) => {
    switch (className) {
      case "class_1":
        return "Class 1";
      case "class_2":
        return "Class 2";
      case "class_3":
        return "Class 3";
      default:
        return className;
    }
  };

  const formatAutomationStatus = (atStatus) => {
    switch (atStatus) {
      case "not-automatable":
        return "Not-Automatable";
      case "automatabel":
        return "Automatable";
      case "in-development":
        return "In-Development";
      case "review":
        return "Review";
      case "ready":
        return "Ready";
      default:
        return atStatus;
    }
  };

  const formatStatus = (atStatus) => {
    switch (atStatus) {
      case "todo":
        return "To-Do";
      case "ongoing":
        return "On-Going";
      case "completed":
        return "Completed";
      default:
        return atStatus;
    }
  };

  function handleCloseReasonPopUp() {
    SetReasonPop(false);
  }

  return (
    <Page>
      <div className="testDetail_page">
        {reasonPop && (
          <Dialog
            isOpen={reasonPop}
            title="Add Reason For Change"
            onClose={handleCloseReasonPopUp}
            confirm={{ text: "Confirm", onConfirm: updateTestInfo }}
          >
            <div className="textarea">
              <textarea
                spellCheck={false}
                autoComplete="off"
                type="text"
                id="inputField"
                name="inputField"
                value={reason}
                onChange={(e) => setReason(e.target.value)}
              ></textarea>
            </div>
          </Dialog>
        )}

        <div className="test_detail">
          <div className="inner_testdetail">
            <div className="testcase-info-box">
              <div className="test_case_name-and-info">
                {enableEdit === false && (
                  <div
                    className="save-button"
                    style={{
                      position: "absolute",
                      right: "10px",
                      bottom: "10px",
                      fontSize: "0.8vw",
                      cursor: "pointer"
                    }}
                  >
                    <Button size="small" type="primary" onClick={() => SetReasonPop(true)} iconPosition="left">
                      Save
                    </Button>
                  </div>
                )}

                <div className="testcase-name-status">
                  <div className="navigate-edit">
                    <p onClick={() => navigate(`/test-case/${params.type}`)}>
                      <BsArrowLeftShort style={{ fontSize: "1rem" }} /> Back
                    </p>
                    <p onClick={() => setEnableEdit(!enableEdit)}>
                      <BiEdit style={{ fontSize: "1vw" }} />
                    </p>
                  </div>
                  <div className="name-id">
                    <h2>{testDetail?.id}</h2>
                    <h2>{testDetail?.test_name}</h2>
                  </div>

                  <div className="st">
                    <p style={{ width: "40%", color: "#5d7290" }}>Status</p>
                    {enableEdit ? (
                      <p
                        style={{
                          backgroundColor: "#ebf0f5",
                          fontSize: "0.9vw",
                          padding: "1px 6px",
                          border: "1.5px solid #d7dee7",
                          borderRadius: "4px",
                          color: "#455365"
                        }}
                      >
                        {formatStatus(testDetail?.status)}
                      </p>
                    ) : (
                      <div className="form-group-drop">
                        <select
                          id="status"
                          name="status"
                          value={status}
                          onChange={handleStatusChange}
                          style={{
                            outline: "none",
                            border: "1.5px solid #d7dee7",
                            padding: "1px 5px",
                            borderRadius: "4px",
                            transition: "border-color 0.3s, box-shadow 0.3s",
                            fontSize: "0.9vw",
                            color: "#525b6c",
                            fontWeight: "500",
                            backgroundColor: "#ebf0f5"
                          }}
                          onFocus={(e) => {
                            e.target.style.border = "1.5px solid #d7dee7";
                            e.target.style.boxShadow = "0 0 2px #d7dee7";
                          }}
                          onBlur={(e) => {
                            e.target.style.border = "1.5px solid #d7dee7";
                            e.target.style.boxShadow = "none";
                          }}
                        >
                          <option value="" disabled>
                            Status
                          </option>
                          <option value="todo">To Do</option>
                          <option value="ongoing">On Going</option>
                          <option value="completed">Completed</option>
                        </select>
                      </div>
                    )}
                  </div>

                  <div className="st">
                    <p style={{ width: "40%", color: "#5d7290" }}>Automation Status</p>
                    {enableEdit ? (
                      <p
                        style={{
                          backgroundColor: "#ebf0f5",
                          fontSize: "0.9vw",
                          padding: "1px 6px",
                          border: "1.5px solid #d7dee7",
                          borderRadius: "4px",
                          color: "#455365"
                        }}
                      >
                        {formatAutomationStatus(testDetail?.automation_status)}
                      </p>
                    ) : (
                      <div className="form-group-drop">
                        {/* <label htmlFor="priority" style={{ marginRight: "10px" }}>
                  TestCase Type:
                </label> */}
                        <select
                          id="Automation"
                          name="Automation"
                          value={automationStatus}
                          onChange={handleAutomationStatusChange}
                          style={{
                            outline: "none",
                            border: "1.5px solid #d7dee7",
                            padding: "1px 5px",
                            borderRadius: "4px",
                            transition: "border-color 0.3s, box-shadow 0.3s",
                            fontSize: "0.9vw",
                            color: "#525b6c",
                            fontWeight: "500",
                            backgroundColor: "#ebf0f5"
                          }}
                          onFocus={(e) => {
                            e.target.style.border = "1.5px solid #d7dee7";
                            e.target.style.boxShadow = "0 0 2px #d7dee7";
                          }}
                          onBlur={(e) => {
                            e.target.style.border = "1.5px solid #d7dee7";
                            e.target.style.boxShadow = "none";
                          }}
                        >
                          <option value="" disabled>
                            Automation Status
                          </option>
                          <option value="not-automatable">Not-Automatable</option>
                          <option value="automatable">Automatable</option>
                          <option value="in-development">In-Development</option>
                          <option value="review">Review</option>
                          <option value="ready">Ready</option>
                        </select>
                      </div>
                    )}
                  </div>

                  <div className="st">
                    <p style={{ width: "40%", color: "#5d7290" }}>Priority</p>
                    {enableEdit ? (
                      <p
                        style={{
                          backgroundColor: "#ebf0f5",
                          fontSize: "0.9vw",
                          padding: "2px 6px",
                          border: "1.5px solid #d7dee7",
                          borderRadius: "4px",
                          color: "#455365"
                        }}
                      >
                        {" "}
                        <MdPriorityHigh
                          style={{
                            color:
                              testDetail?.priority === "class_1"
                                ? "#FF8080"
                                : testDetail?.priority === "class_2"
                                  ? "#FABC3F"
                                  : testDetail?.priority === "class_3"
                                    ? "green"
                                    : "black"
                          }}
                        />{" "}
                        {formatClassName(testDetail?.priority)}
                      </p>
                    ) : (
                      <div className="form-group-drop">
                        <select
                          id="priority"
                          name="priority"
                          value={Priorities}
                          onChange={handlePriorityChange}
                          style={{
                            outline: "none",
                            border: "1.5px solid #d7dee7",
                            padding: "1px 5px",
                            borderRadius: "4px",
                            transition: "border-color 0.3s, box-shadow 0.3s",
                            fontSize: "0.9vw",
                            color: "#525b6c",
                            fontWeight: "500",
                            backgroundColor: "#ebf0f5"
                          }}
                          onFocus={(e) => {
                            e.target.style.border = "1.5px solid #d7dee7";
                            e.target.style.boxShadow = "0 0 2px #d7dee7";
                          }}
                          onBlur={(e) => {
                            e.target.style.border = "1.5px solid #d7dee7";
                            e.target.style.boxShadow = "none";
                          }}
                        >
                          <option value="" disabled>
                            Priority
                          </option>
                          <option value="class_1">Class 1</option>
                          <option value="class_2">Class 2</option>
                          <option value="class_3">Class 3</option>
                        </select>
                      </div>
                    )}
                  </div>

                  <div className="st">
                    <p style={{ width: "40%", color: "#5d7290" }}>TestCase Type</p>
                    <p
                      style={{
                        backgroundColor: "#ebf0f5",
                        fontSize: "0.9vw",
                        padding: "1px 6px",
                        border: "1.5px solid #d7dee7",
                        borderRadius: "4px",
                        color: "#455365"
                      }}
                    >
                      {testDetail?.testcase_type.charAt(0).toUpperCase() + testDetail?.testcase_type.slice(1)}
                    </p>
                  </div>
                </div>

                <div className="description">
                  {/* <p className="name">Description</p>
              <p className="value">
                The purpose of this test case is to rewind VOD content to the
                beginning for PL natco. Precondition * User is logged in. * VOD
                content available to watch.
              </p> */}

                  <div className="people">
                    <p className="name">People</p>

                    <div className="role-section">
                      <div className="stp">
                        <p
                          style={{
                            width: "40%",
                            color: "#6c809b",
                            fontSize: "0.9vw"
                          }}
                        >
                          Asignee
                        </p>
                        <p
                          style={{
                            width: "60%",
                            color: "#425062",
                            fontSize: "0.9vw"
                          }}
                        >
                          <CiUser /> Unassigned
                        </p>
                      </div>

                      <div className="stp">
                        <p
                          style={{
                            width: "40%",
                            color: "#6c809b",
                            fontSize: "0.9vw"
                          }}
                        >
                          Reporter
                        </p>
                        <p
                          style={{
                            width: "60%",
                            color: "#425062",
                            fontSize: "0.9vw"
                          }}
                        >
                          <CiUser /> Nibha
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="people">
                    <p className="name">Dates</p>

                    <div className="role-section">
                      <div className="stp">
                        <p
                          style={{
                            width: "40%",
                            color: "#6c809b",
                            fontSize: "0.9vw"
                          }}
                        >
                          Created
                        </p>
                        <p
                          style={{
                            width: "60%",
                            color: "#425062",
                            fontSize: "0.9vw"
                          }}
                        >
                          Undefined
                        </p>
                      </div>

                      <div className="stp">
                        <p
                          style={{
                            width: "40%",
                            color: "#6c809b",
                            fontSize: "0.9vw"
                          }}
                        >
                          Reporter
                        </p>
                        <p
                          style={{
                            width: "60%",
                            color: "#425062",
                            fontSize: "0.9vw"
                          }}
                        >
                          Undefined
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div className="tab-and-outlet">
              <div className="tab_for_test_case">
                <div className="details tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="details">
                      Details
                    </NavLink>
                  </p>
                </div>
                <div className="description_steps tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="description-step">
                      Test steps
                    </NavLink>
                  </p>
                </div>
                {testDetail?.automation_status !== "not-automatable" && (
                  <div className="automation-script tab">
                    <p>
                      <NavLink className="links" activeClassName="active" to="automation-script">
                        Automation Script
                      </NavLink>
                    </p>
                  </div>
                )}
                <div className="test_group tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="test-group">
                      Test group
                    </NavLink>
                  </p>
                </div>
                <div className="Last_50_run tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="last-run">
                      Last 50 run
                    </NavLink>
                  </p>
                </div>
                <div className="linked_defects tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="defects">
                      Linked defects
                    </NavLink>
                  </p>
                </div>
                {testDetail?.automation_status !== "not-automatable" && (
                  <div className="issue tab">
                    <p>
                      <NavLink className="links" activeClassName="active" to="script-issue">
                        Script Issue
                      </NavLink>
                    </p>
                  </div>
                )}
                {testDetail?.automation_status !== "not-automatable" && (
                  <div className="details tab">
                    <p>
                      <NavLink className="links" activeClassName="active" to="natco">
                        Natco
                      </NavLink>
                    </p>
                  </div>
                )}
                <div className="activities tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="activity">
                      Activity
                    </NavLink>
                  </p>
                </div>
              </div>

              <div className="display">
                <Outlet />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
}

export default TestDetails;
